@import url('https://fonts.googleapis.com/css?family=Playfair+Display:400,700|Arimo:400,700');

$main-font: 'Arimo', sans-serif;
$primary-color: #523e8a;
$second-color: #f3bc4f;
$clear-color: #eeeeee;
$dark-color: #201140;
$low-gray: #bdbdbd;
$low-back: #ede7f6;

body {
    margin: 0;
    font-family: $main-font;
    font-size: 16px;
    color: $dark-color;
    background: $clear-color;
}

a {
    text-decoration: none;

    &:hover {
        -webkit-box-shadow: inset 0px -8px 1px 0px $second-color;
        -moz-box-shadow: inset 0px -8px 1px 0px $second-color;
        box-shadow: inset 0px -8px 1px 0px $second-color;
    }
}

p {
    font-size: 16px;
    line-height: 150%;
}

.content {
    margin: auto;
    width: 1024px;
}

.col-3 {
    width: 60%;
    float: left;
}

.col-1 {
    width: 40%;
    float: left;
}

header {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 9;

    nav {
        overflow: hidden;
        padding: 30px 0;
        width: 100%;
        display: block;
        border-bottom: 1px solid $low-gray;
        background: #ffffff;

        ul {
            margin: 0;
            padding: 0;
            float: right;
            list-style: none;

            li {
                float: left;
                display: block;
                margin: 0 0 0 20px;

                a {
                    font-size: 16px;
                    font-weight: 700;
                    text-transform: uppercase;
                    color: $primary-color;
                }
            }
        }
    }
}

.intro {
    overflow: hidden;
    margin-top: 80px;
    padding: 80px 0;
    background: $low-back;

    h1 {
        margin: 0 0 60px;
        font-family: 'Playfair Display', serif;
        font-weight: 400;
        font-size: 52px;
        color: $primary-color;
    }

    p {
        margin: 0 0 60px 0;
        padding: 10px 0 10px 20px;
        color: $primary-color;
        font-size: 24px;
        font-weight: 300;
        border-left: 6px solid $second-color;
    }

    .col-2 {
        width: 50%;
        float: left;
    }

    .profile {
        display: block;
        margin: auto;
        max-width: 300px;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        position: relative;
        -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
            0 0 40px rgba(0, 0, 0, 0.1) inset;
        -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
            0 0 40px rgba(0, 0, 0, 0.1) inset;
        box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
            0 0 40px rgba(0, 0, 0, 0.1) inset;

        &:before,
        &:after {
            content: '';
            position: absolute;
            z-index: -1;
            -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
            -moz-box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
            box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
            top: 10px;
            bottom: 10px;
            left: 0;
            right: 0;
            -moz-border-radius: 100px / 10px;
            border-radius: 100px / 10px;
        }
        &:after {
            right: 10px;
            left: auto;
            -webkit-transform: skew(8deg) rotate(3deg);
            -moz-transform: skew(8deg) rotate(3deg);
            -ms-transform: skew(8deg) rotate(3deg);
            -o-transform: skew(8deg) rotate(3deg);
            transform: skew(8deg) rotate(3deg);
        }
    }
}

.skills {
    padding: 60px 0;
    background: $dark-color;

    .box {
        overflow: hidden;
        margin-top: -90px;
        padding: 30px;
        width: 960px;
        background: #fff;
        -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);

        .col-3 {
            float: left;
            padding: 0 1%;
            width: 31%;
            text-align: center;
            border-right: 1px solid $low-back;

            &:last-child {
                border-right: 0;
            }
        }

        h2 {
            margin: 20px 0;
            font-weight: 700;
            font-size: 18px;
            color: $primary-color;
            text-transform: uppercase;
        }
    }
}

.about-me {
    overflow: hidden;
    background: $low-back;

    .letter {
        position: relative;
        padding: 60px 0;
        width: 670px;
        height: 550px;
        float: left;
        background: $second-color;
        z-index: 5;

        &:before {
            content: '';
            padding: 60px 0;
            position: absolute;
            top: 0;
            left: -2024px;
            width: 2024px;
            height: 550px;
            background: $second-color;
            z-index: -5;
        }

        h2 {
            margin: 150px 0 0 0;
            font-family: 'Playfair Display', serif;
            font-weight: 400;
            font-size: 250px;
            color: $dark-color;
            text-align: center;
            line-height: 100%;
        }

        p {
            display: block;
            margin: 0 60px 20px;
            padding: 0;
            font-size: 22px;
        }
    }

    .col-1 {
        overflow: hidden;
        float: left;
        padding: 0;
        width: 354px;

        ul {
            display: block;
            margin: 0;
            padding: 60px 0 60px 60px;
            list-style: none;

            li {
                margin: 0 0 20px;
                padding: 20px;
                font-size: 18px;
                background: #fff;
                -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
                -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
                box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
                -webkit-transition: 1s;
                transition: 1s;
                cursor: pointer;

                &:hover {
                    color: $second-color;
                    background: $dark-color;
                }
            }
        }
    }

    hr {
        margin: 20px 0;
        border: none;
        height: 1px;
        background-image: linear-gradient(
            to right,
            $low-back,
            $primary-color,
            $low-back
        );

        &:after {
            content: '§';
            display: inline-block;
            position: absolute;
            left: 50%;
            transform: translate(-50%, -50%) rotate(60deg);
            transform-origin: 50% 50%;
            padding: 1rem;
            background-color: $low-back;
        }
    }
}

.projects {
    overflow: hidden;
    padding: 80px 0 50px;
    background: #fff;

    .content {
        position: relative;

        ul.project-list {
            margin: 0 0 0 -30px;
            padding: 0;
            list-style: none;

            li {
                position: relative;
                margin: 0 30px 30px 0;
                width: 321px;
                float: left;
                display: block;
                cursor: pointer;
                -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
                -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
                box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
                height: 200px;

                &:hover {
                    span {
                        display: block;
                    }
                }

                span {
                    display: none;
                    position: absolute;
                    top: 0;
                    left: 0;
                    padding: 20px;
                    width: 281px;
                    height: 160px;
                    background: rgba(82, 62, 138, 0.8);
                    -webkit-transition: 1s;
                    transition: 1s;

                    h3,
                    h4 {
                        margin: 0;
                        padding: 0;
                        color: $clear-color;
                    }

                    h3 {
                        margin-bottom: 20px;
                        font-size: 24px;
                        font-weight: 700;
                    }
                }

                img {
                    width: 321px;
                    height: 200px;
                }
            }
        }

        .lightbox {
            position: relative;
            background: #fff;

            .col-1 {
                float: left;
                margin: 0 60px 0 0;
                width: 450px;

                img {
                    max-width: 450px;
                    -webkit-box-shadow: 10px 10px 0px 0px $second-color;
                    -moz-box-shadow: 10px 10px 0px 0px $second-color;
                    box-shadow: 10px 10px 0px 0px $second-color;
                }

                ul {
                    margin: 30px 0 0;
                    padding: 0;
                    list-style: none;

                    li {
                        margin-right: 10px;
                        padding: 5px;
                        float: left;
                        font-size: 14px;
                        font-weight: 700;
                        text-transform: uppercase;
                        color: #fff;
                        -webkit-border-radius: 5px;
                        -moz-border-radius: 5px;
                        border-radius: 5px;
                        background: $dark-color;
                    }
                }
            }

            .col-2 {
                float: left;
                width: 514px;
            }

            span {
                position: absolute;
                top: 0;
                right: 0;
                padding: 10px 20px;
                font-weight: 700;
                color: #fff;
                cursor: pointer;
                background: #e74c3c;
            }

            h3 {
                margin: 0 0 30px;
                font-family: 'Playfair Display', serif;
                font-size: 36px;
            }
        }
    }
}

.contact {
    overflow: hidden;
    background: $low-back;

    .letter {
        position: relative;
        padding: 60px 0 0;
        width: 354px;
        height: 1110px;
        float: right;
        color: $low-gray;
        background: $dark-color;
        z-index: 5;

        &:before {
            content: '';
            padding: 60px 0;
            position: absolute;
            top: 0;
            right: -2024px;
            width: 2024px;
            height: 1110px;
            background: $dark-color;
            z-index: -5;
        }

        ul {
            margin: 0 0 0 30px;
            list-style: none;

            li {
                padding: 20px 0;
                font-size: 18px;
                border-bottom: 1px solid #402a70;
            }
        }
    }

    .col-1 {
        padding: 60px;
    }

    .cv {
        ul li {
            line-height: 150%;
        }

        h2 {
            font-size: 20px;
        }

        strong {
            font-size: 12px;
            font-weight: 700;
            text-transform: uppercase;
        }

        .company-name {
            border-bottom: 2px solid $second-color;
        }
    }
}

@media screen and (max-width: 1024px) {
    .content {
        width: 100%;
    }

    header nav ul {
        float: inherit;
    }

    .intro .col-2 {
        width: 100%;
        float: inherit;
    }

    .intro h1 {
        text-align: center;
    }

    .skills .box {
        padding: 2%;
        width: 90%;
        margin: -90px auto 0;
    }

    .about-me .letter,
    .about-me .col-1 {
        width: 50%;
    }

    .about-me .letter p {
        font-size: 16px;
    }

    .about-me .col-1 ul {
        padding: 30px;
    }

    .projects .content ul.project-list {
        margin: 0 60px;
    }

    .projects .content ul.project-list li {
        margin-bottom: 30px;
        width: 100%;
    }

    .projects .content ul.project-list li img {
        float: left;
        width: 50%;
    }

    .projects .content ul.project-list li span {
        display: block;
        position: static;
        padding: 0;
        height: 200px;
        float: left;
        width: 50%;
    }

    .projects .content ul.project-list li span h3 {
        padding: 20px;
    }

    .projects .content ul.project-list li span h4 {
        padding: 0 20px;
    }

    .projects .content .lightbox .col-1,
    .projects .content .lightbox .col-2 {
        width: 100%;
    }

    .projects .content .lightbox .col-1 img {
        max-width: 100%;
    }

    .projects .content .lightbox .col-1 ul {
        padding: 0 0 10px;
    }

    .projects .content .lightbox h3,
    .projects .content .lightbox h4 {
        text-align: center;
    }

    .projects .content .lightbox h3 {
        margin-top: 30px;
    }

    .projects .content .lightbox span {
        top: -18px;
    }

    .projects .content .lightbox p,
    .projects .content .lightbox a {
        padding: 0 20px;
    }

    .contact .letter {
        padding: 0;
        width: 100%;
        height: auto;
    }

    .contact .letter ul {
        margin: 0;
    }
}
