@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700|Arimo:400,700);
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

@charset "UTF-8";
body {
  margin: 0;
  font-family: "Arimo", sans-serif;
  font-size: 16px;
  color: #201140;
  background: #eeeeee; }

a {
  text-decoration: none; }
  a:hover {
    box-shadow: inset 0px -8px 1px 0px #f3bc4f; }

p {
  font-size: 16px;
  line-height: 150%; }

.content {
  margin: auto;
  width: 1024px; }

.col-3 {
  width: 60%;
  float: left; }

.col-1 {
  width: 40%;
  float: left; }

header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 9; }
  header nav {
    overflow: hidden;
    padding: 30px 0;
    width: 100%;
    display: block;
    border-bottom: 1px solid #bdbdbd;
    background: #ffffff; }
    header nav ul {
      margin: 0;
      padding: 0;
      float: right;
      list-style: none; }
      header nav ul li {
        float: left;
        display: block;
        margin: 0 0 0 20px; }
        header nav ul li a {
          font-size: 16px;
          font-weight: 700;
          text-transform: uppercase;
          color: #523e8a; }

.intro {
  overflow: hidden;
  margin-top: 80px;
  padding: 80px 0;
  background: #ede7f6; }
  .intro h1 {
    margin: 0 0 60px;
    font-family: 'Playfair Display', serif;
    font-weight: 400;
    font-size: 52px;
    color: #523e8a; }
  .intro p {
    margin: 0 0 60px 0;
    padding: 10px 0 10px 20px;
    color: #523e8a;
    font-size: 24px;
    font-weight: 300;
    border-left: 6px solid #f3bc4f; }
  .intro .col-2 {
    width: 50%;
    float: left; }
  .intro .profile {
    display: block;
    margin: auto;
    max-width: 300px;
    border-radius: 50%;
    position: relative;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset; }
    .intro .profile:before, .intro .profile:after {
      content: '';
      position: absolute;
      z-index: -1;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
      top: 10px;
      bottom: 10px;
      left: 0;
      right: 0;
      border-radius: 100px / 10px; }
    .intro .profile:after {
      right: 10px;
      left: auto;
      -webkit-transform: skew(8deg) rotate(3deg);
      transform: skew(8deg) rotate(3deg); }

.skills {
  padding: 60px 0;
  background: #201140; }
  .skills .box {
    overflow: hidden;
    margin-top: -90px;
    padding: 30px;
    width: 960px;
    background: #fff;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2); }
    .skills .box .col-3 {
      float: left;
      padding: 0 1%;
      width: 31%;
      text-align: center;
      border-right: 1px solid #ede7f6; }
      .skills .box .col-3:last-child {
        border-right: 0; }
    .skills .box h2 {
      margin: 20px 0;
      font-weight: 700;
      font-size: 18px;
      color: #523e8a;
      text-transform: uppercase; }

.about-me {
  overflow: hidden;
  background: #ede7f6; }
  .about-me .letter {
    position: relative;
    padding: 60px 0;
    width: 670px;
    height: 550px;
    float: left;
    background: #f3bc4f;
    z-index: 5; }
    .about-me .letter:before {
      content: '';
      padding: 60px 0;
      position: absolute;
      top: 0;
      left: -2024px;
      width: 2024px;
      height: 550px;
      background: #f3bc4f;
      z-index: -5; }
    .about-me .letter h2 {
      margin: 150px 0 0 0;
      font-family: 'Playfair Display', serif;
      font-weight: 400;
      font-size: 250px;
      color: #201140;
      text-align: center;
      line-height: 100%; }
    .about-me .letter p {
      display: block;
      margin: 0 60px 20px;
      padding: 0;
      font-size: 22px; }
  .about-me .col-1 {
    overflow: hidden;
    float: left;
    padding: 0;
    width: 354px; }
    .about-me .col-1 ul {
      display: block;
      margin: 0;
      padding: 60px 0 60px 60px;
      list-style: none; }
      .about-me .col-1 ul li {
        margin: 0 0 20px;
        padding: 20px;
        font-size: 18px;
        background: #fff;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
        -webkit-transition: 1s;
        transition: 1s;
        cursor: pointer; }
        .about-me .col-1 ul li:hover {
          color: #f3bc4f;
          background: #201140; }
  .about-me hr {
    margin: 20px 0;
    border: none;
    height: 1px;
    background-image: -webkit-gradient(linear, left top, right top, from(#ede7f6), color-stop(#523e8a), to(#ede7f6));
    background-image: linear-gradient(to right, #ede7f6, #523e8a, #ede7f6); }
    .about-me hr:after {
      content: '\A7';
      display: inline-block;
      position: absolute;
      left: 50%;
      -webkit-transform: translate(-50%, -50%) rotate(60deg);
              transform: translate(-50%, -50%) rotate(60deg);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      padding: 1rem;
      background-color: #ede7f6; }

.projects {
  overflow: hidden;
  padding: 80px 0 50px;
  background: #fff; }
  .projects .content {
    position: relative; }
    .projects .content ul.project-list {
      margin: 0 0 0 -30px;
      padding: 0;
      list-style: none; }
      .projects .content ul.project-list li {
        position: relative;
        margin: 0 30px 30px 0;
        width: 321px;
        float: left;
        display: block;
        cursor: pointer;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
        height: 200px; }
        .projects .content ul.project-list li:hover span {
          display: block; }
        .projects .content ul.project-list li span {
          display: none;
          position: absolute;
          top: 0;
          left: 0;
          padding: 20px;
          width: 281px;
          height: 160px;
          background: rgba(82, 62, 138, 0.8);
          -webkit-transition: 1s;
          transition: 1s; }
          .projects .content ul.project-list li span h3,
          .projects .content ul.project-list li span h4 {
            margin: 0;
            padding: 0;
            color: #eeeeee; }
          .projects .content ul.project-list li span h3 {
            margin-bottom: 20px;
            font-size: 24px;
            font-weight: 700; }
        .projects .content ul.project-list li img {
          width: 321px;
          height: 200px; }
    .projects .content .lightbox {
      position: relative;
      background: #fff; }
      .projects .content .lightbox .col-1 {
        float: left;
        margin: 0 60px 0 0;
        width: 450px; }
        .projects .content .lightbox .col-1 img {
          max-width: 450px;
          box-shadow: 10px 10px 0px 0px #f3bc4f; }
        .projects .content .lightbox .col-1 ul {
          margin: 30px 0 0;
          padding: 0;
          list-style: none; }
          .projects .content .lightbox .col-1 ul li {
            margin-right: 10px;
            padding: 5px;
            float: left;
            font-size: 14px;
            font-weight: 700;
            text-transform: uppercase;
            color: #fff;
            border-radius: 5px;
            background: #201140; }
      .projects .content .lightbox .col-2 {
        float: left;
        width: 514px; }
      .projects .content .lightbox span {
        position: absolute;
        top: 0;
        right: 0;
        padding: 10px 20px;
        font-weight: 700;
        color: #fff;
        cursor: pointer;
        background: #e74c3c; }
      .projects .content .lightbox h3 {
        margin: 0 0 30px;
        font-family: 'Playfair Display', serif;
        font-size: 36px; }

.contact {
  overflow: hidden;
  background: #ede7f6; }
  .contact .letter {
    position: relative;
    padding: 60px 0 0;
    width: 354px;
    height: 1110px;
    float: right;
    color: #bdbdbd;
    background: #201140;
    z-index: 5; }
    .contact .letter:before {
      content: '';
      padding: 60px 0;
      position: absolute;
      top: 0;
      right: -2024px;
      width: 2024px;
      height: 1110px;
      background: #201140;
      z-index: -5; }
    .contact .letter ul {
      margin: 0 0 0 30px;
      list-style: none; }
      .contact .letter ul li {
        padding: 20px 0;
        font-size: 18px;
        border-bottom: 1px solid #402a70; }
  .contact .col-1 {
    padding: 60px; }
  .contact .cv ul li {
    line-height: 150%; }
  .contact .cv h2 {
    font-size: 20px; }
  .contact .cv strong {
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase; }
  .contact .cv .company-name {
    border-bottom: 2px solid #f3bc4f; }

@media screen and (max-width: 1024px) {
  .content {
    width: 100%; }
  header nav ul {
    float: inherit; }
  .intro .col-2 {
    width: 100%;
    float: inherit; }
  .intro h1 {
    text-align: center; }
  .skills .box {
    padding: 2%;
    width: 90%;
    margin: -90px auto 0; }
  .about-me .letter,
  .about-me .col-1 {
    width: 50%; }
  .about-me .letter p {
    font-size: 16px; }
  .about-me .col-1 ul {
    padding: 30px; }
  .projects .content ul.project-list {
    margin: 0 60px; }
  .projects .content ul.project-list li {
    margin-bottom: 30px;
    width: 100%; }
  .projects .content ul.project-list li img {
    float: left;
    width: 50%; }
  .projects .content ul.project-list li span {
    display: block;
    position: static;
    padding: 0;
    height: 200px;
    float: left;
    width: 50%; }
  .projects .content ul.project-list li span h3 {
    padding: 20px; }
  .projects .content ul.project-list li span h4 {
    padding: 0 20px; }
  .projects .content .lightbox .col-1,
  .projects .content .lightbox .col-2 {
    width: 100%; }
  .projects .content .lightbox .col-1 img {
    max-width: 100%; }
  .projects .content .lightbox .col-1 ul {
    padding: 0 0 10px; }
  .projects .content .lightbox h3,
  .projects .content .lightbox h4 {
    text-align: center; }
  .projects .content .lightbox h3 {
    margin-top: 30px; }
  .projects .content .lightbox span {
    top: -18px; }
  .projects .content .lightbox p,
  .projects .content .lightbox a {
    padding: 0 20px; }
  .contact .letter {
    padding: 0;
    width: 100%;
    height: auto; }
  .contact .letter ul {
    margin: 0; } }

